.add-to-cart {
  background-color: rgb(255, 188, 246);
  text-decoration: none;

  display: flex;
  justify-content: space-evenly;
  align-items: center;

  border: none;
  cursor: pointer;

  transition: all 0.5s ease;

  ion-icon {
    font-size: 4rem;
    transform: translate(1rem, -0.5rem);
    color: rgb(26, 26, 49);
    transition: all 0.5s ease;
  }

  h3 {
    transform: translateX(-1rem);
    transition: all 0.5s ease;
  }

  &:hover {
    box-shadow: 6.5vw 0.1rem 0.1rem rgb(255, 174, 244) inset;

    ion-icon {
      transform: translate(0rem, -0.5rem);
    }

    h3 {
      transform: translateX(0rem);
    }
  }

  &:focus,
  &:active {
    outline: none;
    box-shadow: 25vw 0.1rem 0.1rem rgb(255, 174, 244) inset;
  }
}
